// 支付插件相关接口
import axios from './../fetch'

// 支付插件列表
export const payGet = params => { return axios.get('/admin/platform/pay/list', { params: params }) }

// 支付插件编辑
export const payUpdata = params => { return axios.post('/admin/platform/pay/update', params) }



