<template>
  <div>
    <div class="cms-form-layout">
      <cms-prompt collapse="true">
        <li>设置系统使用的支付方式</li>
        <li>可开启/关闭支付方式</li>
      </cms-prompt>
      <el-tabs v-model="activeName">
        <el-tab-pane label="账户余额" name="balance">
          <el-form ref="balanceForm" :model="formData" label-width="210px">
            <el-form-item label="是否开启">
              <cms-switch v-model="formData.balance.isEnable" @change="saveData('balance')"></cms-switch>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="支付宝" name="alipay">
          <el-form ref="alipayForm" :rules="rules" :model="formData.alipay" label-width="210px">
            <el-form-item label="是否开启">
              <cms-switch v-model="formData.alipay.isEnable"></cms-switch>
            </el-form-item>
            <el-form-item label="应用Id" prop="appId">
              <el-input
                maxlength="30"
                v-model="formData.alipay.appId"
                :autofocus="true"
                ref="first"
              ></el-input>
            </el-form-item>
            <el-form-item label="应用私钥" prop="paySecretKey">
              <el-input
                maxlength="1700"
                type="textarea"
                :rows="8"
                v-model="formData.alipay.paySecretKey"
              ></el-input>
              <div class="gray">点击获取
                <a href="https://b.alipay.com/order/pidKey.htm" target="_blank">PID和Key</a>
              </div>
            </el-form-item>
            <el-form-item label="应用公钥" prop="paySecretKey">
              <el-input
                maxlength="1700"
                type="textarea"
                :rows="8"
                v-model="formData.alipay.paySecretKey"
              ></el-input>
            </el-form-item>
          </el-form>
          <div class="cms-form-footer">
            <cms-button @click="saveData('alipay')" v-color>保存</cms-button>
          </div>
        </el-tab-pane>
        <el-tab-pane label="微信" name="weChat">
          <el-tabs tab-position="left" v-model="weActive" class="we-menu" @tab-click="handleOpen">
            <el-tab-pane name="0">
              <div slot="label" class="we-menu-name">
                <el-popover
                  placement="right-start"
                  width="340"
                  transition="fade-in-linear"
                  :visible-arrow="false"
                  trigger="hover"
                >
                  <span>适用于微信扫码支付、公众号支付、H5支付</span>
                  <img slot="reference" src="@/assets/images/third-h5.png" alt>
                </el-popover>
                <p class="we-menu-tit">微信公众号</p>
              </div>
              <div class="we-layout" v-if="weData">
                <wechat-pay @refreshData="reshData" :status="weStatus" :data="weData"></wechat-pay>
              </div>
            </el-tab-pane>
            <el-tab-pane name="1">
              <div slot="label" class="we-menu-name">
                <el-popover
                  placement="right-start"
                  width="220"
                  transition="fade-in-linear"
                  :visible-arrow="false"
                  trigger="hover"
                >
                  <span>适用于微信小程序支付</span>
                  <img slot="reference" src="./../../../../assets/images/third-wechat.png" alt>
                </el-popover>
                <p class="we-menu-tit">小程序</p>
              </div>
              <div class="we-layout" v-if="weData">
                <wechat-pay @refreshData="reshData" :status="weStatus" :data="weData"></wechat-pay>
              </div>
            </el-tab-pane>
          </el-tabs>
        </el-tab-pane>
        <!-- <el-tab-pane label="银联" name="unionpay">
          <el-form ref="unionpayForm" :rules="rules" :model="formData.unionpay" label-width="210px">
            <el-form-item label="是否开启">
              <cms-switch v-model="formData.unionpay.isEnable"></cms-switch>
            </el-form-item>
            <el-form-item label="商户号" prop="payAccount">
              <el-input maxlength="50" v-model="formData.unionpay.payAccount"></el-input>
            </el-form-item>
            <el-form-item label="商户秘钥" prop="unionpayPassword">
              <el-input maxlength="18" v-model="formData.unionpay.unionpayPassword"></el-input>
            </el-form-item>
          </el-form>
          <div class="cms-form-footer">
            <cms-button @click="saveData('unionpay')" v-color>保存</cms-button>
          </div>
        </el-tab-pane> -->
      </el-tabs>
    </div>
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import rules from '@/rules/index.js'
import wechatPay from './wechatPay'
import { payGet, payUpdata } from '@/api/setting/pay.js'
var defaultData = {
  unionpay: {}, // 银联
  wechat: {}, // 微信
  miniprogram: {}, // 小程序
  alipay: {}, // 支付宝
  balance: {} // 余额
}
export default {
  name: 'paymentList',
  mixins: [form],
  components: {
    wechatPay
  },
  data() {
    return {
      formData: Object.assign({}, defaultData),
      activeName: 'balance',
      weStatus: 0,
      weActive: '0',
      weData: null,
      rules: {
        // 微信支付里面的校验规则
        unionpayPassword: [rules.required()],
        payAccount: [rules.required()],
        paySecretKey: [rules.required()],
        appId: [rules.required()]
      }
    }
  },
  methods: {
    reshData() {
      // 授权后数据刷新
      this.getFormData()
      this.editData()
    },
    editData() {
      switch (this.weStatus) {
        case 0:
          this.weData = this.formData.wechat
          break
        case 1:
          this.weData = this.formData.miniprogram
          break
      }
    },
    handleOpen(tab) {
      // 微信相关tab切换
      this.weStatus = parseInt(tab.index)
      this.editData()
    },
    getFormData() {
      payGet()
        .then(res => {
          if (res.code === 200) {
            const formData = res.data
            for (let i = 0; i < formData.length; i++) {
              switch (formData[i].payTypeCode) {
                case 'alipay':
                  this.$set(this.formData, 'alipay', formData[i])
                  break
                case 'balance':
                  this.$set(this.formData, 'balance', formData[i])
                  break
                case 'wechat':
                  this.$set(this.formData, 'wechat', formData[i])
                  break
                case 'miniprogram':
                  this.$set(this.formData, 'miniprogram', formData[i])
                  break
                case 'unionpay':
                  this.$set(this.formData, 'unionpay', formData[i])
                  break
                default:
                  return false
              }
            }
            this.editData()
          } else {
            this.$errorMessage(res.message)
          }
        })
        .catch(() => {
          this.loading = false
          this.$errorMessage('加载失败，请重试！')
        })
    },
    saveData(type) {
      const params = {
        details: []
      }
      if (type === 'alipay') {
        params.details.push(this.formData.alipay)
        this.submitForm('alipayForm', params)
      } else if (type === 'balance') {
        params.details.push(this.formData.balance)
        this.submitForm('balanceForm', params)
      } else if (type === 'weChat') {
        params.details.push(this.formData.wechat)
        params.details.push(this.formData.miniprogram)
      } else if (type === 'unionpay') {
        params.details.push(this.formData.unionpay)
        this.submitForm('unionpayForm', params)
      }
    },
    submitForm(formName, params) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          payUpdata(params).then(res => {
            if (res.code === 200) {
              this.$successMessage('保存成功')
            } else {
              this.$errorMessage('保存失败')
            }
          })
        } else {
          return false
        }
      })
    }
  },
  created() {
    this.getFormData()
  }
}
</script>
<style>
.notes_title {
  color: #999999;
}
.gray a {
  color: #169bd5;
}
.we-menu {
  height: 600px;
  position: relative;
  z-index: 10;
}
.we-menu .el-tabs__item {
  height: 80px;
  margin: 0;
}
.we-menu-name {
  position: relative;
  z-index: 10;
  height: 80px;
  padding-top: 15px;
  text-align: center;
  cursor: pointer;
}
.we-menu-tit {
  color: #353535;
  margin: 0;
  font-size: 12px;
  font-weight: normal;
  letter-spacing: 2px;
  line-height: 14px;
}
.we-layout {
  padding-left: 50px;
}
</style>



