<template>
  <div>
    <div v-if="formData.isAuthorization == 2">
      <!-- 公众号 -->
      <div v-if="status===0">
        <h3 class="cms-segmentation">第一步：添加支付授权目录</h3>
        <div style="padding-left:30px;">
          <p class=" t-14 notes">
          请将商城站点域名，前往微信商户平台添加支付授权目录。如商城访问站点为http://www.jeecms.com或 <br>
          http://jspgou.jeecms.com，商户平台需要将公众号支付及H5支付设置成如下所示。
          <a href="https://pay.weixin.qq.com/index.php/core/home/login?return_url=%2Findex.php" target="_blank">添加</a>
          </p>
          <p class=" t-14 notes t-line"><span class="t-18">• </span> 公众号支付授权目录：http://www.jeecms.com/ 或 http://jspgou.jeecms.com/</p>
          <p class=" t-14 notes t-line"><span class="t-18">• </span> H5支付授权域名：jeecms.com</p>
        </div>
        <h3 class="cms-segmentation">第二步：填写支付参数</h3>
      </div>
      <!-- 可填写表单 -->
      <el-form ref="wechatForm" :rules="rules" :model="formData"  label-width="210px" 
        style="margin-top:20px">
        <el-form-item label="是否开启" prop="isEnable">
          <cms-switch v-model="formData.isEnable" ></cms-switch>
        </el-form-item>
        <el-form-item :label="(status?'小程序':'公众号')+'AppId'" >
          <span>{{formData.appId}}</span>
        </el-form-item>
        <el-form-item label="商户号"  prop="payAccount">
          <el-input v-model="formData.payAccount" maxlength="100"></el-input>
          <div class="gray">填写{{status?'小程序':'公众号'}}绑定的的商户号</div>
        </el-form-item>
       <el-form-item  label="商户密钥"  prop="paySecretKey">
          <el-input v-model="formData.paySecretKey" maxlength="100"></el-input>
          <div class="gray">填写{{status?'小程序':'公众号'}}绑定的商户密钥</div>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="subData(formData)"  v-color >保存</cms-button>
      </div>
    </div>
    <!-- 未授权 -->
    <div v-else>
      <div class="t-authorize" v-if="status == 0">
        <div v-if="formData.isAuthorization ==1"  class="t-14">开启微信公众号支付，需要授权绑定已认证的微信公众号！</div>
        <div class="cms-form-footer">
          <cms-button v-if="formData.isAuthorization==1" @click="openPublic('/#/wechatPublic/list','_blank','微信公众号授权')"  v-color >授权公众号</cms-button>
          <cms-button v-else @click="openPublic('/#/wxConfig','_blank','微信开放平台配置')"  v-color >配置微信开放平台</cms-button>
        </div>
      </div>
      <div class="t-authorize" v-if="status == 1">
        <div v-if="formData.isAuthorization ==1"  class="t-14">开启小程序支付，需要授权绑定已认证的小程序！</div>
        <div class="cms-form-footer">
          <cms-button v-if="formData.isAuthorization ==1" 
           @click="openPublic('#/wechatPublic/list?activeStatus=2&type=list','_blank','微信小程序授权')" v-color >授权小程序</cms-button>
           <cms-button v-else @click="openPublic('/#/wxConfig','_blank','微信开放平台配置')"  v-color >配置微信开放平台</cms-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import { payUpdata } from '@/api/setting/pay.js'
export default {
  props: {
    data: {// 当前登录平台信息
      type: [String, Object],
      default: {}
    },
    status: {// 登录平台
      type: [String, Number],
      default: 0
    }
  },
  watch: {
    status: {
      handler(newData, oldData) {
        this.formData = Object.assign({}, this.data)
      },
      immediate: true,
      deep: true
    }
  },
  data() {
    return {
      rules: {
        appSecret: [rules.required()],
        paySecretKey: [rules.required()],
        payAccount: [rules.required()]
      },
      formData: {
        'isAuthorization': '0',
        'appId': '',
        'appKey': '',
        'isEnable': true,
        'reMark': ''
      },
      baseUrl: window.location.protocol + window.location.host
    }
  },
  methods: {
    openBox(name) { // 打开弹窗
      this.$alert('请在新窗口中完成' + name, '系统提示', {
        confirmButtonText: '刷新授权结果',
        showClose: false,
        callback: action => {
          this.$emit('refreshData')
        }
      })
    },
    subData(params) { // 保存表单信息(管理所有提交)
      this.$refs['wechatForm'].validate((valid) => {
        if (valid) {
          payUpdata({ details: [params] }).then(res => {
            if (res.code === 200) {
              this.$successMessage('保存成功')
            } else {
              this.$errorMessage('保存失败')
            }
          })
        }
      })
    },
    openPublic(url, name, title) { // 跳转页面
      this.openBox(title)
      window.open(url, name)
    }
  },
  created() {}
}
</script>
<style lang="scss" scoped>
  .el-form-item .detail{
    color: #666666;
    font-size: 13px;
  }
  .notes_title{
    color: #999999;
  }
  .notes{
    color: #666666;
  }
  .notes a {
    color: #169BD5;
  }
  .we-h{
    font-size: 14px;
    height: 40px;
    line-height: 40px;
    color: #444444;
    margin-left: 15px;
  }
  .t-14{
    font-size: 14px;
    line-height: 18px;
  }
  .t-line{
    line-height: 20px;
    margin-top: 10px;
  }
  .t-18{
    font-size: 18px;
  }
  .t-authorize{
    padding: 80px 30px;
    width: 600px;
    text-align: center;
  }
</style>



